// Imports
import axios from 'axios';
import Fade from 'react-reveal/Fade';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import {MdEmail} from 'react-icons/md';
import Image from '../Images/login.svg';
import {AiFillLock} from 'react-icons/ai';
import {useRef, useEffect, useState} from 'react';
import CircularProgress from '@mui/material/CircularProgress/CircularProgress';





// Styles
const LoginSection = styled.section`
    width:100%;
    height:100vh;
    display:flex;
    overflow:hidden;
    align-items:center;
    justify-content:center;
`

const LeftSection = styled.div`
    flex:0.9;
    height:100%;
    display:flex;
    position:relative;
    align-items:center;
    justify-content:center;

    @media screen and (max-width:1150px){
        justify-content: flex-start;
    }

    @media screen and (max-width:1050px){
        flex:0.8;
    }

    @media screen and (max-width:950px){
        flex:0.7;
    }

    @media screen and (max-width:890px){
        flex:0.6;
    }

    @media screen and (max-width:768px){
        display:none;
    }
`

const CircleOne = styled.div`
    left:-210%;
    width:300%;
    z-index:-1;
    height:250%;
    bottom:-130%;
    border-radius:50%;
    position:absolute;
    background-color:#00acff;

    @media screen and (max-width:1150px){
        display:none;
    }
`

const CircleTwo = styled.div`
    left:-35%;
    width:140%;
    z-index:-1;
    height:100%;
    bottom:-50%;
    border-radius:50%;
    position:absolute;
    background-color:#00acff;

    @media screen and (max-width:1150px){
        left:-90%;
        width:180%;
        bottom:-30%;
        height:140%;
    }

    @media screen and (max-width:1050px){
        left:-100%;
    }

    @media screen and (max-width:950px){
        bottom:0;
        width:190%;
        height:100%;
    }

    @media screen and (max-width:890px){
        left:-120%;
    }
`

const Img = styled.img`
    width:500px;
    margin-left:20px;

    @media screen and (max-width:1150px){
        width:400px;
    }

    @media screen and (max-width:1050px){
        width:300px;
    }

    @media screen and (max-width:890px){
        width:250px;
    }
`

const RightSection = styled.div`
    flex:1;
    height:100%;
    display:flex;
    margin-left:15%;
    flex-direction:column;
    justify-content:center;
    
    @media screen and (max-width:1250px){
        margin-left:10%;
    }

    @media screen and (max-width:1150px){
        margin-left:5%;
    }

    @media screen and (max-width:1050px){
        margin-left:0%;
    }

    @media screen and (max-width:890px){
        flex:0.6;
        margin-left:-5%;
    }

    @media screen and (max-width:810px){
        flex:0.7;
    }
    
    @media screen and (max-width:768px){
        flex:1;
        margin:0;
        align-items:center;
        background-color:#00acff;
    }
`

const LoginContainer = styled.div`
    width:100%;
    height:60%;
    display:flex;
    flex-direction:column;

    @media screen and (max-width:768px){
        width:90%;
        height:70%;
        border-radius:10px;
        background-color:#1e1e1e;
    }

    @media screen and (max-width:400px){
        width:95%;
    }
`

const HeadingContainer = styled.div`
    display:flex;
    align-items:center;
    
    @media screen and (max-width:768px){
        display:block;
        margin-top:-50px;
        margin-bottom:20px;
    }
`

const MainHeading = styled.h1`
    font-size:35px;

    @media screen and (max-width:890px){
        font-size:30px;
    }

    @media screen and (max-width:768px){
        display:none;
        text-align:center;
    }
`

const HeadingSpan = styled.h1`
    font-size:35px;
    color:#59c9ff;
    padding-left:10px;

    @media screen and (max-width:890px){
        font-size:30px;
    }

    @media screen and (max-width:768px){
        color:#fff;
        text-align:center;
    }

    @media screen and (max-width:400px){
        font-size:27px;
    }

    @media screen and (max-width:300px){
        font-size:24px;
    }
`

const LoginHeading = styled.h1`
    font-size:30px;
    font-weight:500;

    @media screen and (max-width:890px){
        font-size:25px;
    }

    @media screen and (max-width:768px){
        margin-top:70px;
        text-align:center;
    }
`

const Form = styled.form`
    height:60%;
    display:flex;
    margin-top:50px;
    position:relative;
    flex-direction:column;
    align-items:flex-start;
    
    @media screen and (max-width:768px){
        height:45%;
        align-items:center;
    }

    @media screen and (max-width:500px){
        height:40%;
    }
`

const Message = styled.span`
    top:-50px;
    font-size:13px;
    margin-left:20%;
    padding:5px 30px;
    border-radius:5px;
    text-align:center;
    position:absolute;
    transition:0.5s linear;
    background-color:#ff4343;
    opacity:${({error}) => error ? '1' : '0'};

    @media screen and (max-width:768px){
        margin-left:0;
    }

    @media screen and (max-width:500px){
        font-size:11px;
    }
`

const Container = styled.div`
    width:100%;
    height:50%;
    display:flex;
    flex-direction:column;
    align-items:flex-start;
    justify-content:center;
    justify-content:space-between;

    @media screen and (max-width:768px){
        align-items:center;
    }
`

const InputContainerOne = styled.div`
    position:relative;
    border-radius:6px;
`

const InputContainerTwo = styled.div`
    position:relative;
    border-radius:6px;
`

const EmailIcon = styled(MdEmail)`
    width:20px;
    height:100%;
    padding:0 12px;
    font-size:15px;
    position:absolute;
    border-radius:6px 0 0 6px;
    background-color:#59c9ff;
`

const PasswordIcon = styled(AiFillLock)`
    z-index:2;
    width:20px;
    height:100%;
    padding:0 12px;
    font-size:15px;
    position:absolute;
    border-radius:6px 0 0 6px;
    background-color:#59c9ff;
`

const InputOne = styled.input`
    width:450px;
    border:none;
    height:20px;
    outline:none;
    color:#59c9ff;
    border-radius:5px;
    transition:0.2s linear;
    padding:15px 0 15px 60px;
    background-color:#383838;
    border:1px solid #383838;

    @media screen and (max-width:890px){
        width:350px;
    }

    @media screen and (max-width:500px){
        height:15px;
        width:250px;
        font-size:12px;
    }

    @media screen and (max-width:400px){
        height:12px;
        width:200px;
        padding:15px 0 15px 50px;
    }

    &:focus{
        border:1px solid #59c9ff;
    }
`

const InputTwo = styled.input`
    width:450px;
    border:none;
    height:20px;
    outline:none;
    padding:15px;
    color:#59c9ff;
    border-radius:5px;
    position:relative;
    transition:0.2s linear;
    padding:15px 0 15px 60px;
    background-color:#383838;
    border:1px solid #383838;

    @media screen and (max-width:890px){
        width:350px;
    }

    @media screen and (max-width:500px){
        height:15px;
        width:250px;
        font-size:12px;
    }

    @media screen and (max-width:400px){
        height:12px;
        width:200px;
        padding:15px 0 15px 50px;
    }

    &:focus{
        border:1px solid #59c9ff;
    }
`

const PostButton = styled.button`
    border:none;
    outline:none;
    font-size:20px;
    cursor:pointer;
    color:#fff;
    margin-top:50px;
    padding:5px 50px;
    text-align:center;
    border-radius:5px;
    transition:0.2s linear;
    background-color:#383838;

    @media screen and (max-width:890px){
        font-size:17px;
    }

    @media screen and (max-width:768px){
        margin-top:20px;
    }

    @media screen and (max-width:450px){
        font-size:13px;
        padding:5px 30px;
    }

    &:hover{
        background-color:#59c9ff;
    }
`

const RegisterArea = styled.div`
    margin-top:20px;

    @media screen and (max-width:768px){
        margin-top:0;
        text-align:center;
    }

    @media screen and (max-width:500px){
        font-size:15px;
    }

    @media screen and (max-width:400px){
        font-size:13px;
    }
`

const TheLink = styled(Link)`
    color:#fff;
    text-decoration:none;
    transition:0.2s linear;

    &:hover{
        color:#59c9ff;
    }
`

const Div = styled.div`
    display:none;

    @media screen and (max-width:768px){
        left:0;
        bottom:0;
        width:100%;
        line-height:0;
        display:block;
        overflow:hidden;
        position:absolute;
        transform:rotate(180deg);
    }
`

const Svg = styled.svg`
    height:150px;
    display:block;
    position:relative;
    width:calc(101% + 1.3px);
    transform:rotateY(180deg);
`

const Path = styled.path`
    fill:#5fcaff;
`

const CircularProgressContainer = styled.div`
    color:#fff;
    display:flex;
    align-items:center;
    justify-content:center;
`





// Main Function
export default function Login() {

    const email = useRef();
    const password = useRef();
    const [input, setInput] = useState({});
    const [error, setError] = useState(false);

    useEffect(() => {
        const LoggingUser = async () => {
            try {
                const res = await axios.post('https://jobook-server-side.herokuapp.com/auth/login', input);
                console.log(res);
                if(res.data !== "Wrong Password"){
                    localStorage.setItem('user', JSON.stringify(res.data));
                    window.location.reload();
                }else if(res.data === "Wrong Password"){
                    setError(true);
                    setTimeout(() => {
                        setError(false);
                    }, 2000);
                }
            } catch (err) {
                console.log(err);
            }
        }
        LoggingUser();
    }, [input]);
    
    const SubmitHandler = e => {
        e.preventDefault();
        setInput({
            email:email.current.value,
            password:password.current.value,
        });
        setTimeout(() => {
            if(!localStorage.getItem('user')){
                setError(true);
                setTimeout(() => {
                    setError(false);
                }, 2000);
            }
        }, 1000)
    }

    return (
        <LoginSection>
            <LeftSection>
                <CircleOne />
                <CircleTwo />
                <Fade left>
                    <Img src={Image}/>
                </Fade>
            </LeftSection>
            <RightSection>
                <HeadingContainer>
                    <Fade top><MainHeading>Welcome To </MainHeading></Fade><Fade top><HeadingSpan>JoBook</HeadingSpan></Fade>
                </HeadingContainer>
                <LoginContainer>
                    <Fade right distance="100px">
                        <LoginHeading>Log In</LoginHeading>
                    </Fade>
                    <Form onSubmit={SubmitHandler}>
                        <Message error={error}>Wrong Email Or Password</Message>
                        <Container>
                            <Fade right distance="100px">
                                <InputContainerOne>
                                    <EmailIcon />
                                    <InputOne type="email" placeholder="Email" ref={email}/>
                                </InputContainerOne>
                            </Fade>
                            <Fade right distance="100px">
                                <InputContainerTwo>
                                    <PasswordIcon />
                                    <InputTwo type="password" placeholder="Password" ref={password}/>
                                </InputContainerTwo>
                            </Fade>
                        </Container>
                        <Fade right>
                            <PostButton>Login</PostButton>
                        </Fade>
                    </Form>
                    <RegisterArea>
                        <Fade bottom ditance="100px">
                            <TheLink to="/register">New To JoBook? Register</TheLink>
                        </Fade>
                    </RegisterArea>
                </LoginContainer>
                <Div class="custom-shape-divider-bottom-1648986354">
                    <Svg data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1200 120" preserveAspectRatio="none">
                        <Path d="M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z" opacity=".25" class="shape-fill"></Path>
                        <Path d="M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z" opacity=".5" class="shape-fill"></Path>
                        <Path d="M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z" class="shape-fill"></Path>
                    </Svg>
                </Div>
            </RightSection>
        </LoginSection>
    )
}
