// Imports
import Fade from 'react-reveal/Fade';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import Image from '../Images/NoUser.png';
import {IoIosLogOut} from 'react-icons/io';
import {AiOutlineSearch, AiFillHome, AiOutlineMenu} from 'react-icons/ai';





// Styles
const NavContainer = styled.nav`
    top:0;
    left:0;
    z-index:9;
    width:100%;
    height:70px;
    display:flex;
    position:fixed;
    align-items:center;
    justify-content:center;
    background-color:#1a1a1a;

    @media screen and (max-width:768px){
        border-bottom:1px solid #fff;
    }
`

const NavWrapper = styled.div`
    width:86%;
    height:100%;
    display:flex;
    font-weight:100;
    align-items:center;
    justify-content:space-between;
`

const NavLogo = styled.h3`
    font-size:25px;

    @media screen and (max-width:500px){
        font-size:20px;
    }
`

const NavPages = styled.ul`
    padding:0;
    width:13%;
    display:flex;
    list-style:none;
    align-items:center;
    justify-content:space-between;

    @media screen and (max-width:900px){
        margin-right:10px;
    }

    @media screen and (max-width:800px){
        margin-right:20px;
    }

    @media screen and (max-width:768px){
        margin-right:50px;
    }

    @media screen and (max-width:600px){
        margin-right:80px;
    }

    @media screen and (max-width:500px){
        display:none;
    }
`

const NavPage = styled.li`
    font-size:19px;
    font-weight:100;

    @media screen and (max-width:500px){
        display:none;
    }
`

const NavLink = styled(Link)`
    color:#fff;
    text-decoration:none;
    transition:0.2s linear;

    &:hover{
        color:#59c9ff;
    }
`

const IconContainer = styled.div`
    display:none;

    @media screen and (max-width:500px){
        display:block;
    }
`

const Icon = styled(AiOutlineMenu)`
    cursor:pointer;
    font-size:25px;
    transition:0.2s linear;

    &:hover{
        color:#59c9ff;
    }
`

const Word = styled.span`
    color:#59c9ff;
    font-size:1.2em;
`

const SearchBarContainer = styled.div`
    width:40%;
    height:30px;
    display:flex;
    position:relative;
    align-items:center;
    border-radius:50px;
    justify-content:center;
    background-color:#1e1e1e;

    @media screen and (max-width:768px){
        display:none;
    }
`

const SearchBar = styled.input`
    width:100%;
    height:100%;
    border:none;
    color:#fff;
    outline:none;
    font-size:12px;
    padding-left:10px;
    border-radius:10px;
    background-color:#464646;
`

const SearchIcon = styled(AiOutlineSearch)`
    right:10px;
    font-size:20px;
    position:absolute;
`

const UserImage = styled.img`
    width:40px;
    border-radius:50%;

    @media screen and (max-width:992px){
        width:35px;  
    }
`

const NavLinkTwo = styled(Link)`
    color:#fff;
    display:flex;
    margin-left:60px;
    align-items:center;
    text-decoration:none;
    justify-content:center;

    @media screen and (max-width:992px){
        margin-left:40px;       
    }
`

const LogOutIcon = styled(IoIosLogOut)`
    color:#fff;
    font-size:25px;
    margin-left:10px;
    transition:0.2s linear;

    &:hover{
        color:#59c9ff;
    }

    @media screen and (max-width:992px){
        font-size:20px;  
    }
`

const HomeIcon = styled(AiFillHome)`
    color:#fff;
    font-size:30px;
    transition:0.2s linear;

    &:hover{
        color:#59c9ff;
    }
`





// Main Function
export default function Nav({OpenHandler}) {

    const ClickHandler = () => {
        localStorage.removeItem('user');
        window.location.reload();
    }

    const user = JSON.parse(localStorage.getItem('user'));

    return (
        <NavContainer>
            <NavWrapper>
                <Fade top><NavLink to="/"><NavLogo><Word>J</Word>oBook</NavLogo></NavLink></Fade>
                <SearchBarContainer>
                    <SearchBar placeholder="Search People"/>
                    <SearchIcon />
                </SearchBarContainer>
                <NavPages>
                    <Fade top><NavLink to='/'><HomeIcon /></NavLink></Fade>
                    <Fade top><NavLinkTwo to={`/profile/${user.firstname}`}><NavPage><UserImage src={Image}/></NavPage></NavLinkTwo></Fade>
                    <Fade top><NavLink to="/login" onClick={ClickHandler}><NavPage><LogOutIcon /></NavPage></NavLink></Fade>
                </NavPages>
                <IconContainer>
                    <Fade top>
                        <Icon onClick={OpenHandler}/>
                    </Fade>
                </IconContainer>
            </NavWrapper>
        </NavContainer>
    )
}
