// Imports
import axios from 'axios';
import Post from './Post';
import Fade from 'react-reveal/Fade';
import styled from 'styled-components';
import Image from '../Images/NoUser.png';
import {useHistory} from 'react-router-dom';
import {FaPhotoVideo} from 'react-icons/fa';
import {useEffect, useState, useRef} from 'react';
import {MdKeyboardArrowDown} from 'react-icons/md';
import CircularProgress from '@mui/material/CircularProgress/CircularProgress';





// Styles
const FeedContainer = styled.div`
    display:flex;
    margin:70px 0 0 300px;
    flex-direction:column;
    width:calc(100% - 300px);

    @media screen and (max-width:768px){
        width:100%;
        margin-left:0;
        align-items:center;
    }
`

const P = styled.p`
    margin:50px 0 0 200px;
`

const PostArea = styled.div`
    width:600px;
    min-height:250px;
    border-radius:10px;
    margin:20px 0 70px 50px;
    background-color:#1a1a1a;

    @media screen and (max-width:992px){
        width:470px;
        margin-left:0;
    }

    @media screen and (max-width:850px){
        margin-left:-30px;
    }

    @media screen and (max-width:768px){
        margin:-20px 0 80px 0;
    }

    @media screen and (max-width:500px){
        width:350px;
        margin-bottom:40px;
    }

    @media screen and (max-width:380px){
        width:230px;
        margin-bottom:40px;
    }
`

const UpperSection = styled.div`
    display:flex;
    margin-left:25px;
    align-items:center;

    @media screen and (max-width:1100px){
        margin-left:10px;
    }

    @media screen and (max-width:500px){
        margin-left:0;
    }
`

const UserImg = styled.img`
    width:45px;
    height:40px;
    border-radius:50%;
    margin:20px 0 0 20px;

    @media screen and (max-width:992px){
        width:35px;  
    }
`

const UserName = styled.p`
    margin:20px 0 0 10px;

    @media screen and (max-width:500px){
        font-size:15px;
        margin-left:5px;
    }
`

const InputsArea = styled.form`
    width:100%;
    height:80%;
    display:flex;
    align-items:center;
    flex-direction:column;
    justify-content:center;
`

const Input = styled.input`
    width:550px;
    height:100px;
    border:none;
    color:#fff;
    outline:none;
    font-size:12px;
    padding-left:10px;
    border-radius:10px;
    background-color:#1a1a1a;

    &::placeholder{
        color:#fff;
    }

    @media screen and (max-width:1100px){
        width:90%;
    }

    @media screen and (max-width:500px){
        font-size:10px;
        font-weight:100;
        padding-left:20px;
    }
`

const ImgArea = styled.div`
    position:relative;
`

const Img = styled.img`
    width:500px;
    border-radius:5px;

    @media screen and (max-width:1100px){
        width:400px;
    }

    @media screen and (max-width:600px){
        width:300px;
    }

    @media screen and (max-width:350px){
        width:250px;
    }
`

const Span = styled.span`
    top:10px;
    right:10px;
    font-size:15px;
    cursor:pointer;
    padding:5px 10px;
    position:absolute;
    border-radius:50%;
    transition:0.2s linear;
    background-color:#000000b8;

    &:hover{
        color:#59c9ff;
    }

    @media screen and (max-width:600px){
        font-size:12px;
    }
`

const ChooseFileIcon = styled.label`
    cursor:pointer;
`

const ShareBtn = styled.button`
    border:none;
    outline:none;
    color:#fff;
    font-size:15px;
    transition:0.2s linear;
    background-color:transparent;
    opacity:${({isWritten}) => isWritten ? '1' : '0.5'};
    cursor:${({isWritten}) => isWritten ? 'pointer' : 'not-allowed'};
    pointer-events:${({isWritten}) => isWritten ? 'unset' : 'none'};

    &:hover{
        color:#59c9ff;
        color:${({isWritten}) => isWritten ? '#59c9ff' : '#fff'};
    }

    @media screen and (max-width:500px){
        font-size:12px;
    }
`

const PostButton = styled.div`
    color:#fff;
    display:flex;
    font-size:15px;
    cursor:pointer;
    align-items:center;
    justify-content:center;
    transition:0.2s linear;

    &:hover{
        color:#59c9ff;
    }

    @media screen and (max-width:500px){
        font-size:12px;
    }
`

const ImageIcon = styled(FaPhotoVideo)`
    margin-right:5px;
`

const InputFile = styled.input`
    display:none;   
`

const LowerSection = styled.div`

`

const ButtonsArea = styled.div`
    width:80%;
    display:flex;
    align-items:center;
    justify-content:space-between;
    margin:${({file}) => file ? '20px 0 50px 0' : '20px 0 20px 0'};
`

const Mood = styled.div`
    display:flex;
    position:relative;
    align-items:center;
    justify-content:center;

    @media screen and (max-width:500px){
        font-size:12px;
    }
`

const Hr = styled.hr`
    width:90%;
    border:none;
    background-color:#fff;
    height:${({file}) => file ? '0' : '1px'};
`

const ArrowDown = styled(MdKeyboardArrowDown)`
    font-size:20px;
    margin-left:5px;
`

const MoodsMenu = styled.div`
    top:0;
    width:50px;
    right:-30px;
    height:135px;
    position:absolute;
    border-radius:5px;
    align-items:center;
    flex-direction:column;
    background-color:#383838;
    display:${({isOpen}) => isOpen ? 'flex' : 'none'};

    @media screen and (max-width:500px){
        height:100px;
    }
`

const UpperArea = styled.div`
    display:flex;
    align-items:center;
    justify-content:center;
    transition:0.2s linear;

    &:hover{
        cursor:pointer;
        color:#59c9ff;
    }
`

const Happy = styled.span`
    cursor:pointer;
    transition:0.2s linear;

    &:hover{
        transform:scale(1.2);
    }

    &:after{
        opacity:0;
        content:"Happy";
        position:absolute;
        transform:scale(0.8);
        transition:0.2s linear;
    }

    &:hover:after{
        opacity:1;
    }
`

const Sad = styled.span`
    cursor:pointer;
    transition:0.2s linear;

    &:hover{
        transform:scale(1.2);
    }

    &:after{
        opacity:0;
        content:"Sad";
        position:absolute;
        transform:scale(0.8);
        transition:0.2s linear;
    }

    &:hover:after{
        opacity:1;
    }
`

const Excited = styled.span`
    cursor:pointer;
    transition:0.2s linear;

    &:hover{
        transform:scale(1.2);
    }

    &:after{
        opacity:0;
        content:"Excited";
        position:absolute;
        transform:scale(0.8);
        transition:0.2s linear;
    }

    &:hover:after{
        opacity:1;
    }
`

const Angry = styled.span`
    cursor:pointer;
    transition:0.2s linear;

    &:hover{
        transform:scale(1.2);
    }

    &:after{
        opacity:0;
        content:"Angry";
        position:absolute;
        transform:scale(0.8);
        transition:0.2s linear;
    }

    &:hover:after{
        opacity:1;
    }
`

const Chill = styled.span`
    cursor:pointer;
    transition:0.2s linear;

    &:hover{
        transform:scale(1.2);
    }

    &:after{
        opacity:0;
        content:"Chill";
        position:absolute;
        transform:scale(0.8);
        transition:0.2s linear;
    }

    &:hover:after{
        opacity:1;
    }
`

const Thankful = styled.span`
    cursor:pointer;
    transition:0.2s linear;

    &:hover{
        transform:scale(1.2);
    }

    &:after{
        opacity:0;
        content:"Thankful";
        position:absolute;
        transform:scale(0.8);
        transition:0.2s linear;
    }

    &:hover:after{
        opacity:1;
    }
`

const CircularProgressContainer = styled.div`
    color:#fff;
`





// Main Function
export default function Feed({profileUser}) {

    const history = useHistory();
    const description = useRef();
    const [mood, setMood] = useState('');
    const [file, setFile] = useState(null);
    const [posts, setPosts] = useState([{}]);
    const [isOpen, setIsOpen] = useState(false);
    const [isClicked, setIsClicked] = useState(false);
    const [isWritten, setIsWritten] = useState(false);
    const currentUser = JSON.parse(localStorage.getItem('user'));

    const OpenHandler = () => {
        setIsOpen(!isOpen);
    }

    useEffect(() => {
        const PostsFetcher = async () => {
            try {
                const res = profileUser
                    ? await axios.get(`https://jobook-server-side.herokuapp.com/posts/user-posts/${profileUser.userId}`)
                    : await axios.get(`https://jobook-server-side.herokuapp.com/posts/friends-posts/${currentUser.userId}`);
                setPosts(res.data);
            } catch (err) {
                console.log(err);
            }
        }
        PostsFetcher();
    }, [profileUser ? profileUser.userId : currentUser.userId])

    const ClickHandler = () => {
        setTimeout(() => {
            setIsClicked(true);
        }, 500);
    }

    const SubmitHandler = async e => {
        e.preventDefault();
        const input = {
            userId:currentUser.userId,
            desc:description.current.value,
            postId:Math.floor(Math.random() * 1000000000),
            feeling:mood !== <ArrowDown /> ? mood : ''
        }
        console.log(mood);
        if(file){
            const data = new FormData();
            const fileName = Date.now() + file.name;
            data.append('name', fileName);
            data.append('file', file);
            input.img = fileName;
            try {
                await axios.post('https://jobook-server-side.herokuapp.com/api/upload', data);
            } catch (err) {
                console.log(err);
            }
        }
        try {
            await axios.post('https://jobook-server-side.herokuapp.com/posts', input);
        } catch (err) {
            console.log(err);
        }
        history.push(`/profile/${currentUser.firstname}`);
        window.location.reload();
    }

    const HappyClick = () => {
        setMood('');
        setIsOpen(false);
    }

    const SadClick = () => {
        setMood('');
        setIsOpen(false);
    }

    const ExcitedClick = () => {
        setMood('');
        setIsOpen(false);
    }

    const AngryClick = () => {
        setMood('');
        setIsOpen(false);
    }

    const ChillClick = () => {
        setMood('');
        setIsOpen(false);
    }

    const ThankfulClick = () => {
        setMood('');
        setIsOpen(false);
    }

    return (
        <FeedContainer>
            <Fade top>
                <PostArea>
                    <div />
                    <UpperSection onClick={() => setIsOpen(false)}>
                        <UserImg src={Image}/>
                        <UserName>{currentUser.firstname}</UserName>
                    </UpperSection>
                    <LowerSection>
                        <InputsArea onSubmit={SubmitHandler} encType="multipart/form-data">
                            <Input type="text" placeholder={`What's on Your Mind ${currentUser.firstname.split(' ')[0]}?`} onChange={e =>  e.target.value.length !== 0 ? setIsWritten(true) : setIsWritten(false)} ref={description} onClick={() => setIsOpen(false)}/>
                            {file && 
                                <ImgArea onClick={() => setIsOpen(false)}>
                                    <Fade bottom><Img src={URL.createObjectURL(file)} className="imgFile" alt="User's Post"/></Fade>
                                    <Span onClick={() => setFile(null)}>X</Span>
                                </ImgArea>
                            }
                            <Hr file={file}/>
                            <ButtonsArea file={file}>
                                <ChooseFileIcon htmlFor="file" onClick={() => setIsOpen(false)}>
                                    <InputFile type="file" id="file" accept=".png, .jpg, .jpeg" onChange={e => setFile(e.target.files[0])} name='file'/>
                                    <PostButton><ImageIcon />Photo</PostButton>
                                </ChooseFileIcon>
                                <Mood>
                                    <UpperArea onClick={OpenHandler}>Mood:{mood === '' ? <ArrowDown /> : ''}{mood}</UpperArea>
                                    <MoodsMenu isOpen={isOpen}>
                                        <Happy onClick={HappyClick}></Happy>
                                        <Sad onClick={SadClick}></Sad>
                                        <Excited onClick={ExcitedClick}></Excited>
                                        <Angry onClick={AngryClick}></Angry>
                                        <Chill onClick={ChillClick}></Chill>
                                        <Thankful onClick={ThankfulClick}></Thankful>
                                    </MoodsMenu>
                                </Mood>
                                {isClicked ? <CircularProgressContainer><CircularProgress color={'inherit'} size={20}/></CircularProgressContainer> : <Fade bottom><ShareBtn isWritten={isWritten} onClick={ClickHandler}>Share</ShareBtn></Fade>}
                            </ButtonsArea>
                        </InputsArea>
                    </LowerSection>
                </PostArea>
            </Fade>
            {
                posts.length > 0 
                ? posts.map(post => (
                    <Post profileUser={profileUser} post={post} />
                ))
                : <P>No Posts To Show</P>
            }
        </FeedContainer>
    )
}