// Imports
import axios from 'axios';
import {useParams} from 'react-router';
import styled from 'styled-components';
import {useState, useEffect} from 'react';
import NoUser from '../Images/NoUser.png';
import NoCover from '../Images/NoCover.png';





// Styles
const ProfileContainer = styled.div`
    top:0;
    height:200px;
    position:absolute;
    margin:70px 0 0 300px;
    width:calc(100vw - 305px);

    @media screen and (max-width:992px){
        width:calc(100vw - 5px);
        margin:unset;
    }

    @media screen and (max-width:500px){
        height:250px;
    }

    @media screen and (max-width:380px){
        height:300px;
    }
`

const BackgroundImg = styled.img`
    width:100%;
    height:100%;
    border-radius:5px;
    object-fit:${({profileUser}) => profileUser ? '' : 'cover'};
`

const ProfileWrapper = styled.div`
    display:flex;
    align-items:center;

    @media screen and (max-width:768px){
        align-items:unset;
        flex-direction:column;
    }
`

const ProfileImg = styled.img`
    width:100px;
    height:100px;
    object-fit:cover;
    border-radius:50%;
    margin:-50px 0 0 60px;
    border:5px solid #1e1e1e;

    @media screen and (max-width:500px){
        width:70px;
        height:70px;
        margin:-30px 0 0 30px;
    }

    @media screen and (max-width:380px){
        width:70px;
        height:70px;
        margin:-30px 0 0 10px;
    }
`

const Name = styled.p`
    font-size:20px;
    margin-top:10px;

    @media screen and (max-width:500px){
        font-size:17px;
    }

    @media screen and (max-width:280px){
        font-size:15px;
    }
`

const Wrapper = styled.div`
    height:50px;
    display:flex;

    @media screen and (max-width:430px){
        flex-direction:column;
    }
`

const WrapperOne = styled.div`
    display:flex;
`

const WrapperTwo = styled.div`
    display:flex;
    color:#ccc;
    font-size:15px;
    font-weight:100;
    margin-left:200px;
    align-items:center;
    justify-content:center;

    @media screen and (max-width:768px){
        margin-left:100px;
    }

    @media screen and (max-width:550px){
        font-size:12px;
        margin-left:50px;
    }

    @media screen and (max-width:430px){
        margin-left:0;
    }
`





export default function ProfileImgs({profileUser}) {

    const [user, setUser] = useState({});
    const PF = 'http://localhost:3000/Images/';
    const userFirstname = useParams().firstname;
    const currentUser = JSON.parse(localStorage.getItem('user'));
    const [followed, setFollowed] = useState(false);

    const FollowFunction = async () => {
        try {
            if(followed){
                await axios.put(`https://jobook-server-side.herokuapp.com/users/unfollow/${user && user.userId}`, {userId:currentUser.userId});
                setFollowed(!followed);
            }else{
                await axios.put(`https://jobook-server-side.herokuapp.com/users/follow/${user && user.userId}`, {userId:currentUser.userId});
                setFollowed(!followed);
            }
        } catch(err){
            console.log(err)
        }
    }

    const UserFetcher = async () => {
        try {
            const res = await axios.get(`https://jobook-server-side.herokuapp.com/users/name?firstname=${userFirstname}`);
            setUser(res.data);
        } catch (err){
            console.log(err);
        }
    }
    UserFetcher();

    useEffect(() => {
        const Follow = async () => {
            setFollowed(user.followers ? user.followers.includes(currentUser.userId) : false);
        }
        Follow();
    })

    return (
        <ProfileContainer>
            <BackgroundImg src={profileUser.backgroundPic ? PF + profileUser.backgroundPic : NoCover} profileUser={profileUser.backgroundPic}/>
            <ProfileWrapper>
                <Wrapper>
                    <WrapperOne>
                        <ProfileImg src={profileUser.profilePic ? PF + profileUser.profilePic : NoUser}/>
                        {profileUser.firstname !== undefined ? <Name>{`${profileUser.firstname}`}</Name> : ''}
                    </WrapperOne>
                    <WrapperTwo>
                        {profileUser.bio}
                    </WrapperTwo>
                </Wrapper>
            </ProfileWrapper>
        </ProfileContainer>
    )
}